@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(253, 228, 165, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(223, 228, 165, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(223, 228, 165, 0);
  }
}

.pulse {
  animation: pulse 1.5s ease-in-out infinite;
}

.pulse:hover {
  animation-duration: 1s;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font: inherit;
}

html {
  scroll-behavior: smooth;
}

main {
  flex: 1 0 auto;
}

section {
  padding-top: 5.625em;
  padding-bottom: 5.625em;
}

picture {
  display: inline-block;
  width: 100%;
  line-height: 0;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
  transition: opacity 0.3s ease-in-out;
}

a:hover {
  opacity: 0.6;
}

button {
  cursor: pointer;
  outline: none;
  border: none;
  background-color: transparent;
}

button:focus {
  outline: none;
}

ul {
  display: flex;
  align-items: center;
  list-style: none;
}

input {
  outline: unset;
  border: unset;
}

input::placeholder {
  opacity: 0.6;
}

footer {
  flex: 0 0 auto;
}

body {
  font: 16px/1.5 "WorkSans", "Arial", "Roboto", sans-serif;
}

.header--is-fixed {
  position: fixed;
  z-index: 1000;
  width: 100%;
}

.header--is-sticky {
  position: sticky;
}

.page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  margin-right: auto;
  margin-left: auto;
}

.u-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-right: 0.9375em;
  padding-left: 0.9375em;
  width: 100%;
  max-width: 71.25em;
  margin-right: auto;
  margin-left: auto;
}

.u-wrapper--is-row {
  flex-direction: row;
}
